<template>
    <div class="order">

        <van-tabs v-if="isLogin" v-model="activeName" :sticky="true" @change="getOrder">
            <van-tab :title="item.title" :name="item.name" v-for="(item, index) in tabList" :key="index">
                <div class="order-content">

                    <div class="order-item" v-for="(item, index) in orderNum" :key="index">

                        <div class="order-title">
                            <div class="order-number">{{ item }}</div>

                            <div class="goods-status" v-if="orderData[item].status == 1">
                                <span @click="receipt(item)">确认收货</span>
                            </div>

                            <div class="goods-status" v-else>
                                <span>已完成</span>
                                <span class="del" @click="del(item)">
                                    <van-icon name="delete" />
                                </span>
                            </div>
                        </div>

                        <div class="order-goods">
                            <div class="goods" v-for="(val, i) in orderData[item].data" :key="i">
                                <div class="goods-img">
                                    <img class="auto-img" :src="val.smallImg" alt="">
                                </div>

                                <div class="goods-content">
                                    <div class="goods-text">
                                        <div class="goods-title">
                                            <span class="name">{{ val.name }}</span>
                                            <div class="price">￥{{ val.price }}</div>
                                        </div>
                                        <div class="enname">{{ val.enname }}</div>
                                    </div>
                                    
                                    <div class="goods-rule">
                                        <span class="rule">{{ val.rule }}</span>
                                        <span class="count">×{{ val.count }}</span>
                                    </div>
                                </div>
                            </div>

                            <div class="foot">
                                <div class="time">{{ orderData[item].time | formatDate("yyyy-MM-dd hh:mm:ss") }}</div>

                                <div class="total">
                                    <span class="total-count">共计 {{ orderData[item].count }} 件商品 合计：</span>
                                    <span class="total-pic">￥{{ orderData[item].total }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </van-tab>
        </van-tabs>

        <van-empty v-else image="error" description="还没有登录, 请先登录">
            <span class="goLogin" @click="$router.push({ name: 'Login' })">去登陆&gt;</span>
        </van-empty>


        <NavBar></NavBar>
    </div>
</template>

<script>
    import { Toast } from "vant";
    // 导入导航组件
    import NavBar from "@/components/NavBar";

    export default {
        name: "Order",

        data() {
            return {
                tabList: [
                    { title: "全部", name: "0" },
                    { title: "待收货", name: "1" },
                    { title: "已收货", name: "2" },
                ],
                
                activeName: "0",
                // 订单号
                orderNum: null,
                // 订单数据
                orderData: {},

                isLogin: false,
            };
        },
        
        methods: {

            // 获取订单
            getOrder(status) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    // 延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/findOrder",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                        status: this.activeName,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 70000) {

                        this.isLogin = true;
                        
                        // 降序
                        res.data.result.sort((a, b) => {
                            return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
                        });

                        // 订单号
                        let oids = [];
                        // 订单数据
                        let orderData = {};

                        res.data.result.forEach(item => {
                            if(oids.indexOf(item.oid) === -1){
                                oids.push(item.oid);
                            }
                            
                            if(!orderData[item.oid]){
                                orderData[item.oid] = {
                                    data: [item],
                                    status: item.status,
                                    time: item.createdAt,
                                    count: item.count,
                                    total: item.count * item.price,
                                }
                            } else{

                                orderData[item.oid].count += item.count;
                                orderData[item.oid].total += item.count * item.price;

                                orderData[item.oid].data.push(item);
                            }

                        });

                        this.orderNum = oids;

                        this.orderData = orderData;

                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();

                    this.isLogin = false;


                });
            },

            // 确认收货
            receipt(oid) {

                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    // 延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/receive",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        oid,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 80000) {
                        Toast.success(res.data.msg);

                        this.orderData[oid].status = 2;

                        // 删除页面上的订单
                        if(this.activeName == 1) {
                            let index = this.orderNum.indexOf(oid);
                            this.orderNum.splice(index, 1);
                        }
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 删除
            del(oid) {
                 // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    // 延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/removeOrder",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        oid,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 90000) {
                        Toast.success(res.data.msg);

                        // 删除页面的订单
                        let index = this.orderNum.indexOf(oid);
                        this.orderNum.splice(index, 1);

                    }
                
                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });

            }

        },

        components: {
            NavBar,
        },

        filters: {
            // 格式化时间
            formatDate(value, fmt) {
                let date = new Date(value);

                let year = date.getFullYear();

                if (/(y+)/.test(fmt)) {

                    fmt = fmt.replace(RegExp.$1, year);

                }

                let o = {
                    M: date.getMonth() + 1,
                　　d: date.getDate(),
                　　h: date.getHours(),
                　　m: date.getMinutes(),
                　　s: date.getSeconds(),
                }

                for(let key in o){

                    if(new RegExp(`(${key}+)`).test(fmt)) {
                        let content = RegExp.$1;

                        let val = o[key] >= 10 ? o[key] : content.length == 2 ?  "0" +  o[key] : o[key];

                        // let val = o[key] >= 10 ? o[key] : "0" +  o[key];

                        fmt = fmt.replace(content, val);

                    }
                }

                return fmt;
            }
        },

        created() {

            // 获取订单
            this.getOrder();

            this.$store.state.active = 1;
            
        }
    }
</script>

<style lang="less" scoped>
    /deep/ .van-tabs__wrap{
        height: 45px;
    }

    .order-content{
        padding: 0 10px 55px;
        color: #646566;
         
        .order-item{
            margin-top: 10px;
            padding: 10px;
            background-color: #fff;
            border-radius: 10px;

            .order-title{
                display: flex;
                line-height: 30px;
                justify-content: space-between;
                align-items: center;

                .goods-status{
                    display: flex;
                    color: #A8A8A8;

                    .del{
                        display: flex;
                        align-items: center;
                        margin-left: 10px;
                        font-size: 16px;
                    }
                }
            }

            .order-goods{
                border-top: 1px solid #F3F3F3;
                // border-bottom: 1px solid #F3F3F3;

                .goods{
                    display: flex;
                    padding-top: 10px;

                    .goods-img{
                        width: 70px;
                        height: 70px;
                    }

                    .goods-content{
                        display: flex;
                        flex: 1;
                        margin-left: 10px;
                        flex-direction: column;
                        justify-content: space-between;

                        .goods-title, .goods-rule{
                            display: flex;
                            justify-content: space-between;
                        }

                        .name{
                            font-size: 16px;
                        }

                        .enname{
                            margin-top: 2px;
                            color: #A8A8A8;
                        }

                        .rule, .count{
                            color: #A8A8A8;
                        }

                    }

                }

                .foot{
                    display: flex;
                    margin-top: 10px;
                    line-height: 30px;
                    justify-content: space-between;

                    .time{
                        color: #A8A8A8;
                    }

                    .total{
                        text-align: right;

                        .total-count{
                            color: #A8A8A8;
                        }

                        .total-pic{
                            font-size: 16px;
                            font-weight: bold;
                        }
                    }
                }
            }
        }
    }

    .goLogin{
        color: #1989FA;
        font-size: 14px;
        text-decoration: underline;
    }
</style>